import React, { useState, useRef } from 'react';
import ImgsViewer from 'react-images-viewer';
import './styles.css';

function Content() {
  const fileInputRef = useRef(null);
  const [selectedImages, setSelectedImages] = useState([]);
  const [viewImages, setViewImages] = useState([]);
  const [isOpen, setIsOpen] = useState(false);
  const [currImg, setCurrImg] = useState(0);

  const handleFileChange = async (event) => {
    const files = event.target.files;
    const selected = [...selectedImages]; // Copy the existing array of selected images
    for (let i = 0; i < files.length; i++) {
      const imageUrl = await resizeImage(files[i]);
      selected.push({ src: imageUrl });
    }
    setSelectedImages(selected);
  };

  const resizeImage = (file) => {
    return new Promise((resolve) => {
      const reader = new FileReader();
      reader.onload = (event) => {
        const img = new Image();
        img.onload = () => {
          const canvas = document.createElement('canvas');
          const ctx = canvas.getContext('2d');
          const maxWidth = 200; // Maximum width for resized image
          const maxHeight = 200; // Maximum height for resized image
          let width = img.width;
          let height = img.height;

          // Calculate new dimensions while maintaining aspect ratio
          if (width > height) {
            if (width > maxWidth) {
              height *= maxWidth / width;
              width = maxWidth;
            }
          } else {
            if (height > maxHeight) {
              width *= maxHeight / height;
              height = maxHeight;
            }
          }

          canvas.width = width;
          canvas.height = height;

          // Draw image on canvas
          ctx.drawImage(img, 0, 0, width, height);

          // Convert canvas to data URL
          const resizedImageUrl = canvas.toDataURL('image/jpeg');

          resolve(resizedImageUrl);
        };
        img.src = event.target.result;
      };
      reader.readAsDataURL(file);
    });
  };

  const handleButtonClick = () => {
    fileInputRef.current.click(); // Click the hidden file input when the button is clicked
  };

  const handleImageClick = (index) => {
    setCurrImg(index);
    setIsOpen(true);
  };

  const handleAddImage = () => {
    handleButtonClick(); // Trigger file input when "+" button is clicked
  };

  const handleDone = () => {
    setViewImages(selectedImages);
    setSelectedImages([]); // Clear selected images
  };

  const handleCloseImgsViewer = () => {
    setIsOpen(false);
  };

  return (
    <div className="content" style={{ backgroundImage: `url('./icons/back.JPG')` }}>
      <h1>IMPORT YOUR PHOTOS</h1>
      <p>make sure they are jpg, heif</p>
      {/* Hidden file input */}
      <input ref={fileInputRef} type="file" accept=".jpg,.heif" style={{ display: 'none' }} onChange={handleFileChange} multiple />
      {/* Display selected images */}
      <div className="selected-images">
        {selectedImages.map((image, index) => (
          <img
            key={index}
            src={image.src}
            alt={`Selected Image ${index}`}
            onClick={() => handleImageClick(index)}
            loading="lazy" // Lazy loading
          />
        ))}
        {/* "+" button */}
        <button className="add-image-button" onClick={handleAddImage}>Add image</button>
      </div>
      {/* Done button */}
      <button className="done-button" onClick={handleDone}>Done</button>
      {/* View images */}
      <ImgsViewer
        imgs={viewImages}
        isOpen={isOpen}
        currImg={currImg}
        onClickPrev={() => setCurrImg(currImg - 1)}
        onClickNext={() => setCurrImg(currImg + 1)}
        onClose={handleCloseImgsViewer}
        showThumbnails={true}
      />
    </div>
  );
}

export default Content;
